@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;500;700&family=Roboto+Slab:wght@400;700&family=Roboto:wght@400;500;700;900&display=swap);
body {
	font-family: "Roboto Mono", monospace;
	margin: 0px;
	padding: 0px;
}

a {
	text-decoration: none;
	display: inline;
	color: black;
}


header {
	display: grid;
	width: 100%;
	height: 140px;
	grid-template-rows: repeat(2, 1fr);
}

header .Navbar-icons-container {
	width: 100%;
	height: 50px;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	background-color: #404040;
}

header .Navbar-icons-container .icons {
	width: 150px;
	height: auto;
	display: -webkit-flex;
	display: flex;
	justify-items: flex-end;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	justify-self: end;
	margin-right: 30px;
}

header .icons span {
	color: white;
	transition: all 0.3s;
}

header .icons span:hover {
	color: lightseagreen;
}

header .icons a {
	transition: all 0.3s;
}

header .icons a:hover {
	-webkit-transform: scale(1.1, 1.1);
	        transform: scale(1.1, 1.1);
}

header .owner {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin-left: 30px;
	cursor: pointer;
}

header .owner a {
	font-size: 25px;
	font-weight: bold;
	color: white;
	transition: all 0.3s;
}

header .owner a:hover {
	color: yellowgreen;
	-webkit-transform: scale(1.1, 1.1);
	        transform: scale(1.1, 1.1);
}

nav {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	height: 90px;
}

nav .nav-logo-container {
	margin-left: 45px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	justify-items: flex-start;
}

nav .nav-logo-container img {
	width: 150px;
	margin-top: 10px;
	transition: all 0.3s;
}

nav .nav-logo-container img:hover {
	-webkit-transform: scale(1.2, 1.2);
	        transform: scale(1.2, 1.2);
}

nav .profile-link {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	margin-right: 40px;
}

nav .profile-link .topic {
	margin-left: 3px;
	padding: 5px;
	padding-left: 7px;
	padding-right: 7px;
	background-color: #ebba16;
	color: black;
	font-weight: 700;
	overflow: hidden;
	transition: 0.3s;
}

nav .profile-link .topic:hover {
	background-color: black;
	color: white;
}
