.Home-body {
	position: fixed;
	left: 0;
	right: 0;
}

.Home-main {
	display: grid;
	grid-template-columns: 1fr 4fr 1fr;
	height: 100%;
	background-image: url(/static/media/Cover.b2d97236.png);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

.Home-main section {
	display: grid;
	grid-column: 2;
	justify-items: center;
	height: 350px;
	margin-top: 80px;
}

.Home-main-text {
	/* font-family: "Roboto Mono", monospace; */
	font-size: 30px;
	font-weight: 700;
	letter-spacing: 5px;
	color: black;
	text-align: center;
	color: white;
	-webkit-animation: Appear 1s linear;
	        animation: Appear 1s linear;
}

.Home-main-button {
	width: 130px;
	height: 50px;
	background-color: #ebba16;
	display: grid;
	-webkit-align-items: center;
	        align-items: center;
	border-radius: 10px;
	border: 3px solid white;
	transition: 0.3s;
	opacity: 1;
	-webkit-animation-duration: 3s;
	        animation-duration: 3s;
	-webkit-animation-name: Appear;
	        animation-name: Appear;
	color: white;
}

.Home-main-button:hover {
	background-color: white;
	-webkit-transform: scale(1.1, 1.1);
	        transform: scale(1.1, 1.1);
}

.Home-main-button a {
	display: block;
	height: 100%;
	line-height: 43px;
	width: 100%;
	font-weight: 700;
	margin: auto;
	font-size: 15px;
	color: white;
}

.Home-main-button a:hover {
	color: black;
}

@-webkit-keyframes Appear {
	0% {
		opacity: 0;
		/* transform: translateY(+200%); */
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
		/* transform: translateY(0%); */
	}
}

@keyframes Appear {
	0% {
		opacity: 0;
		/* transform: translateY(+200%); */
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
		/* transform: translateY(0%); */
	}
}

/* Blogs */

.blogs-main {
	/* display: grid; */
}

.blogs-news-container {
	background-color: #e6e9ed;
	padding: 0px 50px 40px;
}

.blogs-main-new {
	display: grid;
	grid-template-columns: 2fr 1fr;
	grid-template-rows: 2fr;
}

.blogs-news-img-container {
	grid-column: 1;
	display: grid;
	-webkit-align-items: center;
	        align-items: center;
}

.blogs-news-img-container img {
	width: 85%;
	margin: auto;
	max-width: 425px;
}

.blogs-news-info-container {
	grid-column: 2;
}

.blogs-news-info-container p {
	margin-bottom: 35px;
}

.blogs-posts-container {
	padding: 0 50px 40px;
}

.blogs-posts-container h3 {
	border-bottom: 1px solid #cdd2da;
	padding-bottom: 20px;
	text-align: center;
}

.blogs-button {
	border: 1px solid #ebba16;
	padding: 10px 15px;
	font-size: 12px;
	font-weight: 700;
	transition: all 0.3s;
}

.blogs-button:hover {
	border: 1px solid black;
	font-weight: 700;
	background-color: #ebba16;
	border: 1px solid black;
}

.blogs-posts-container .post-container {
	display: inline-block;
	padding-left: 10px;
	max-width: 30%;
	margin-bottom: 50px;
}

.blogs-posts-container .post-container p {
	margin-bottom: 35px;
}

.blogs-posts-container .post-container img {
	width: 100%;
}

